import { Component, OnInit } from '@angular/core';
import {Invoiceservices} from  '../service/invoice';
import {Router} from '@angular/router';
@Component({
  selector: 'app-invoices',
  templateUrl: './invoices.component.html',
  styleUrls: ['./invoices.component.css'],
  providers : [Invoiceservices]
})
export class InvoicesComponent implements OnInit {


  key: string = 'Invoice_No';
  reverse: boolean = false;
  
  p: number = 1;
  public AllInvoices : any[];
  constructor(private invoiceService:Invoiceservices,private router: Router) { 
    this.invoiceService.Allinvoices().subscribe(
      result=>
      {
       this.AllInvoices = result.json().Transaction.Result;
       console.log(this.AllInvoices)
      }, error=> console.error(error));
  }

  ngOnInit() {
  }

  routeToInvDetail(invNo,bciID){
    this.router.navigateByUrl('Invoicedetails'); 
  }

  sort(key){
    this.key = key;
    this.reverse = !this.reverse;
  }

}



