import { Component, OnInit } from '@angular/core';
import {Invoiceservices} from  '../service/invoice';
 

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  providers : [Invoiceservices]
})
export class DashboardComponent implements OnInit {

  width = 640;
  height = 400;
  type = 'pie3d';
  dataFormat = 'json';
  dataSource1 = this.dataSource1; 
  dataSource2 = this.dataSource2;

  constructor(private invoiceService:Invoiceservices) {
    this.dataSource1 = {
      "chart": {
        "startingangle": "120",
        "showlabels": "0",
        "showlegend": "0",
        "enablemultislicing": "0",
        "slicingdistance": "15",
        "showpercentvalues": "1",
        "showpercentintooltip": "0",
        "plottooltext": "Invoices : $label Total visit : $datavalue",
        "theme": "ocean"
      },
      "data": [
        {
            "label": "Invoices due Today:",
            "value": "1250400"
        },
        {
            "label": "Date View by Turn Around Time Invoices past due:",
            "value": "1463300"
        },
        {
            "label": "Invoices due in 1 day",
            "value": "1050700"
        },
        {
            "label": "Invoices due in 2 to 5 days:",
            "value": "491000"
        },
        {
          "label": "Invoices due in >5 days:",
          "value": "491000"
      }
    ]
  }

  this.dataSource2 = {
    "chart": {
      "startingangle": "120",
      "showlabels": "0",
      "showlegend": "0",
      "enablemultislicing": "0",
      "slicingdistance": "15",
      "showpercentvalues": "1",
      "showpercentintooltip": "0",
      "plottooltext": "Invoices : $label Count : $datavalue",
      "theme": "ocean"
    },
    "data": [
      {
          "label": "Invoices missed TAT 3 days and above:",
          "value": "1250400"
      },
      {
          "label": "Invoices missed TAT 2 days:",
          "value": "1463300"
      },
      {
          "label": "Invoices missed TAT 1 days:",
          "value": "1050700"
      },
      {
          "label": "Invoices TAT today:",
          "value": "491000"
      },
      {
        "label": "Invoices TAT 1 day:",
        "value": "1463300"
    },
    {
        "label": "Invoices TAT 2 days:",
        "value": "1050700"
    },
    {
        "label": "Invoices TAT 3 days and above:",
        "value": "491000"
    }
  ]
}
   }

  ngOnInit() {
  }

}



