  getInvoice(){
    this.invoiceService.Getinvoice().subscribe(
      result=>
      {
       this.InvoiceData = result.json().Transaction.Result;
       this.InvoiceLineDetails = result.json().Transaction.Result.Invoice.DCDocumentData.DC_Lines;       
       console.log("invoice data")
       console.log(this.InvoiceData);
       console.log(this.InvoiceLineDetails)
      }, error=> console.error(error));
  }

  enableActiontabs(){
    
    this.invoiceService.Findactionfilename().subscribe(
      result=>
      {
       this.Mappingactions = result.json().CONFIG.RECORDS;
       var filterRecords = this.findActionfilename(this.Mappingactions,this.status,this.reason);
       if(filterRecords.length>0) this.findActiontabs(filterRecords[0].FILENAME);
      }, error=> console.error(error));
   }

   findActiontabs(fileName)
   {
    this.invoiceService.Dynamictabdetails(fileName).subscribe(
      result=>
      {
       this.mappedTabs = result.json().CONFIG.ENABLE;
       console.log(this.mappedTabs)
       for(var i=0;i<this.mappedTabs.length;i++)
       {
         document.getElementById(this.mappedTabs[i].id).style.display=this.mappedTabs[i].style;
         document.getElementById(this.mappedTabs[i].id+'Main').style.display=this.mappedTabs[i].style;
         if(this.mappedTabs[i].activeclass.length > 0) {
           var elementLI = document.getElementById(this.mappedTabs[i].id+'Main');
           var elementDiv = document.getElementById(this.mappedTabs[i].id);
           elementLI.className = this.mappedTabs[i].activeclass;
           elementDiv.className =  this.mappedTabs[i].displayclass;
         }
       }
      }, error=> console.error(error));
   }
   
  findActionfilename(data, Status,Reason) {
    return data.filter(e => e.STATUS === Status && e.REASON === Reason);
  }

