editOnLineDetail(index)
{


  $(".Invoice_Line"+index+"").prop('disabled', false)
  $(".POLine"+index+"").prop('disabled', false)
  $(".PO_Number"+index+"").prop('disabled', false)
  $(".Description"+index+"").prop('disabled', false)
  $(".Quantity"+index+"").prop('disabled', false)
  $(".Unit_Price"+index+"").prop('disabled', false)
 


  $(".Invoice_Line"+index+"").css("border","1px solid #004266");
  $(".POLine"+index+"").css("border","1px solid #004266");
  $(".PO_Number"+index+"").css("border","1px solid #004266");
  $(".Description"+index+"").css("border","1px solid #004266");
  $(".Quantity"+index+"").css("border","1px solid #004266");
  $(".Unit_Price"+index+"").css("border","1px solid #004266");
 

  $('#lineDetail'+index+'').prop('contenteditable', true)
  }


  
saveOnLineDetail(inv_line,po_line,po_no,desc,qty,unit_prc,total_amt,index){
  //this.addrowLineItem=[];
    console.log("checking  edited data=======================>")
    console.log(inv_line,po_line,po_no,desc,qty,unit_prc,total_amt,index);
  
    var totalAmount=String(+qty*+unit_prc); 
    var totalAmount1=parseInt.apply(totalAmount);

    console.log("invoice structure");
    console.log( this.InvoiceData.Invoice.DCDocumentData.DC_Lines);

    
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.Invoice_Line=inv_line;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.POLine=po_line;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.PO_Number=po_no;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.Description=desc;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.Quantity=qty;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.Unit_Price=unit_prc;
    this.InvoiceData.Invoice.DCDocumentData.DC_Lines.Amount=totalAmount;
                      
  
  
  $(".Invoice_Line"+index+"").prop('disabled', true)
  $(".POLine"+index+"").prop('disabled', true)
  $(".PO_Number"+index+"").prop('disabled', true)
  $(".Description"+index+"").prop('disabled', true)
  $(".Quantity"+index+"").prop('disabled', true) 
  $(".Unit_Price"+index+"").prop('disabled', true) 
   
    
  $(".Invoice_Line"+index+"").css("border","0px solid");
  $(".POLine"+index+"").css("border","0px solid");
  $(".PO_Number"+index+"").css("border","0px solid");
  $(".Description"+index+"").css("border","0px solid");
  $(".Quantity"+index+"").css("border","0px solid"); 
  $(".Unit_Price"+index+"").css("border","0px solid"); 
  


  $('#lineDetail').prop('contenteditable', false) 


  console.log("after save invoice structure");
  console.log( this.InvoiceData.Invoice.DCDocumentData.DC_Lines);

  }


